/* eslint-disable camelcase */
/* eslint-disable react/no-access-state-in-setstate */
/* eslint-disable react/destructuring-assignment */
import React from 'react';
import {
    Table,
    Button,
    Row,
    Col,
    Form,
    Select,
    Switch,
    Input,
    Divider, Popconfirm,
    Modal
} from 'antd';
import {
    CheckOutlined,
    CloseOutlined,
    RedoOutlined,
    PlusOutlined,
    EditOutlined, DeleteFilled,
    LineOutlined
} from '@ant-design/icons';

import { observer, inject } from 'mobx-react';
import { withTranslation } from 'react-i18next';
import uniqid from 'uniqid';
import moment from 'moment';
import qs from 'qs';
import i18n from '../../i18n';
import './model.css';
import DBModel from "./DBModel"
import getAgentInstance from '../../helpers/superagent'
import { deleteRequest, getRequest, putRequest } from '../../helpers/superagentAPI'
import RemoteSelect from "../basic/RemoteSelect";

const superagent = getAgentInstance();

const pageSizeOptions = ['8', '15', '20', '30', '40'];

@inject('userStore')
@observer
class List extends React.Component {
    constructor(props) {
        super(props);
        this.initialState = () => ({
            modelName: 'ServiceAchivments',
            createTitle: i18n.t('New Record'),
            editTitle: i18n.t('Edit Record'),
            data: [],
            loading: false,
            type_name: undefined,
            selectedPackageId: -1,
            selectedUserId: -1,
            pagination: {
                pageSize: parseInt(pageSizeOptions[0], 10),
                pageSizeOptions,
                current: 1,
                total: 0,
                showSizeChanger: true,
                hideOnSinglePage: false,
                showQuickJumper: true,
            },
            sortedInfo: null,
            editResourceId: undefined,
            activityForDetails: null,
            access: []//JSON.parse(this.props.userStore.value.privilege),
        });
        this.state = this.initialState();

        this.fetch = async () => {
            this.setState({ loading: true });
            const { pagination, modelName } = this.state;

            const params = {
                pageSize: pagination.pageSize,
                page: pagination.current - 1,
            };

            try {
                this.setState({ loading: true })
                let body = await getRequest(`${modelName}?${qs.stringify(params)}`)
                const tpagination = { ...this.state.pagination };
                tpagination.total = body.count;
                this.setState({
                    loading: false,
                    data: body.data,
                    pagination: tpagination,
                });
            } catch (error) {
                this.setState(this.initialState())
            } finally {
                this.setState({ loading: false })
            }
        };

        this.deleteModel = async (id) => {
            try {
                this.setState({
                    deleting: true
                })
                await deleteRequest(`${this.state.modelName}/${id}`)
                this.fetch()
            } catch (error) {
            } finally {
                this.setState({
                    deleting: false
                })
            }
        }

        this.showActivityDetail = (activity) => {
            this.setState({
                activityForDetails: activity
            })
        }

        this.closeActivityDetails = () => {
            this.setState({
                activityForDetails: null
            })
        }

        this.columns = [
            {
                title: '#',
                align: 'center',
                dataIndex: 'id',
                key: 'id',
                sorter: true,
                width: 70,
                render: (value) => <p style={{ fontFamily: 'Cuprumsemi' }}>{value}</p>,
            },
            {
                title: i18n.t('Service Name'),
                dataIndex: `service`,
                key: `service`,
                align: 'start',
                ellipsis: true,
                render: (value) => <p style={{ fontFamily: 'Cuprumsemi' }}>{value.nameEn}</p>,
            },
            {
                title: i18n.t('Description English'),
                dataIndex: `descriptionEn`,
                key: `descriptionEn`,
                align: 'start',
                ellipsis: true
            },
            {
                title: i18n.t('Description Arabic'),
                dataIndex: `descriptionAr`,
                key: `descriptionAr`,
                align: 'start',
                ellipsis: true
            },
            {
                title: i18n.t('Actions'),
                sorter: false,
                align: 'center',
                width: 160,
                render: (object) => (
                    <Row span={24} gutter={5}>
                        <Col span={12}>
                            <DBModel id={object.id} reloadGrid={this.fetch} title={this.state.editTitle}
                                modelName={this.state.modelName} buttonIcon={(<EditOutlined />)} />
                        </Col>
                        <Col span={12}>
                            <Popconfirm placement="bottom" title={i18n.t('delete') + '?'} onConfirm={() => this.deleteModel(object.id)}
                                okText={i18n.t('Yes')}
                                cancelText={i18n.t('No')}>
                                <Button icon={<DeleteFilled style={{
                                    color: 'red'
                                }} />}
                                    shape="round"
                                    className="btn_hover darkBlueBg"
                                    type={'primary'}
                                    loading={this.state.deleting} />
                            </Popconfirm>
                        </Col>
                    </Row>
                ),
            },
        ];

        // custom refs
        this.newBtn = React.createRef();
        this.editBtn = React.createRef();

        this.newBtnClicked = () => {
            this.newBtn.current.click();
        };

        this.editBtnClicked = async (id) => {
            await this.setState({
                editResourceId: id,
            });
            this.editBtn.current.click();
        };

        this.handleTableChange = (pagination, filters, sorter) => {
            const pager = { ...this.state.pagination };
            pager.current = pagination.current;
            pager.pageSize = pagination.pageSize;
            this.setState(
                {
                    pagination: pager,
                    sortedInfo: sorter,
                },
                () => this.fetch(),
            );
        };

        this.categoryNameChanged = () => {
            this.fetch()
        }

        this.filterFormRef = React.createRef();
    }

    componentDidMount() {
        this.fetch();
    }

    render() {
        const {
            data, pagination, loading, createTitle, modelName, activityForDetails
        } = this.state;
        return (
            <>
                <Row gutter={[10, 10]}>
                    <Col span={24}>
                        <Table
                            columns={this.columns}
                            rowClassName="tb_row"
                            rowKey={() => uniqid()}
                            dataSource={data}
                            pagination={pagination}
                            loading={loading}
                            onChange={this.handleTableChange}
                            title={() => (
                                <Row>
                                    <Col span={4} style={{ display: 'flex' }}><h2
                                        style={{ margin: 'auto' }}>{i18n.t(modelName)}</h2><Divider type={'vertical'} style={{
                                            height: '80%',
                                            margin: 'auto 10%'
                                        }} /></Col>
                                    <Col offset={16} span={4}><DBModel reloadGrid={this.fetch} title={createTitle}
                                        modelName={modelName}
                                        buttonIcon={(<PlusOutlined />)} /></Col>
                                </Row>
                            )}
                        />
                    </Col>
                </Row>
                <p className="pager-Info">
                    {i18n.t('Showing')}
                    {' '}
                    {data.length}
                    {' '}
                    {i18n.t('from')}
                    {' '}
                    {pagination.total}
                </p>
            </>
        );
    }
}

export default withTranslation()(List);
