/* eslint-disable react/destructuring-assignment */
import React from 'react';
import { Select, Spin, Empty } from 'antd';
import debounce from 'lodash/debounce';
import getAgentInstance from '../../helpers/superagent';

const superagent = getAgentInstance();

class RemoteSelect extends React.Component {
  constructor(props) {
    super();
    this.initialState = () => ({
      data: [],
      value: undefined,
      initialValue: props.defaultValue || undefined,
      fetching: false,
      all: props.all ? props.all : false
    });
    this.state = this.initialState();

    this.onChange = (value) => {
      this.setState({ value });
      try {
        const v = value ? value.key : undefined;
        const { onChange } = this.props;
        onChange(value, v);
        if (!value) {
          this.loadData('');
        }
      } catch (e) {
        //
      }
    };
    this.resetValue = () => {
      this.setState({ value: undefined });
    };

    this.onSearch = (searchQuery) => {
      this.loadData(searchQuery);
    };

    this.loadData = debounce((searchQuery, endpoint = undefined) => {
      this.setState({ fetching: true });
      const params = {
        limit: 100,
        offset: 0,
      };

      if (searchQuery && searchQuery !== '') {
        params.q = searchQuery;
      }
      superagent
        .get(`${process.env.REACT_APP_API_URL}${endpoint || this.props.endpoint}`)
        .query(params)
        .end((err, res) => {
          if (!err) {
            const { body } = res;
            const { exclude } = this.props;

            let array = [];
            if (exclude) {
              const filtered = body.filter((value) => value.id !== exclude);
              array = filtered;
            } else array = body;

            const options = [];
            array.data.map((element) => {
              options.push({
                text:
                  typeof this.props.optiontext === 'function'
                    ? this.props.optiontext(element)
                    : element.name,
                value:
                  typeof this.props.optionvalue === 'function'
                    ? this.props.optionvalue(element)
                    : element.id,
              });
              return null;
            });
            this.setState({ data: options, fetching: false });
          } else {
            this.setState({ data: [], fetching: false });
          }
        });
    }, 800);

    this.ref = props.ref;
  }

  componentDidMount() {
    if (this.props.endpoint) this.loadData();
  }

  static getDerivedStateFromProps(props) {
    if ('value' in props) {
      const { value } = props;
      return { value };
    }
    return null;
  }

  render() {
    const { fetching, data, value, initialValue, all } = this.state;
    const {
      placeholder, disabled, className, rtl,
    } = this.props;
    const textHolder = placeholder || '';
    return (
      <Select
        allowClear={this.props.allowClear === undefined ? true : this.props.allowClear}
        mode={this.props.mode || null}
        showSearch
        disabled={disabled}
        labelInValue
        value={value}
        defaultValue={initialValue}
        notFoundContent={
          fetching ? (
            <Spin size="small" />
          ) : (
            <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
          )
        }
        filterOption={false}
        onSearch={this.onSearch}
        onChange={this.onChange}
        style={{ width: '100%' }}
        placeholder={textHolder}
        className={className || undefined}
      >
        {all && (<Select.Option key={-9} style={{ fontFamily: rtl ? 'Cuprumsemi' : undefined }}>All</Select.Option>)}
        {data.map((d) => (
          <Select.Option
            key={d.value}
            style={{ fontFamily: rtl ? 'Cuprumsemi' : undefined }}
          >
            {d.text}
          </Select.Option>
        ))}
      </Select>
    );
  }
}

export default RemoteSelect;
